	module.exports = {"data":{"markdownRemark":{"html":"<h2>What is an SDK</h2>\n<p>A Software Development Kit (SDK) is a set of tools and libraries that you can use to create applications for a specific software package. </p>\n<p>Some SDKs are critical for developing a platform-specific app. For example, the development of an Android app on Java platform requires a Java Development Kit, for iOS apps the iOS SDK, and for Universal Windows Platform the .NET Framework SDK. </p>\n<p>There are also SDKs that are installed in apps to provide analytics and data about activity. Prominent examples include Google and Facebook.</p>\n<p>It may be something as simple as the implementation of one or more application programming interfaces (APIs) in the form of some libraries to interface to a particular programming language or to include sophisticated hardware that can communicate with a particular embedded system. </p>\n<p>Common tools include debugging facilities and other utilities, often presented in an integrated development environment (IDE). SDKs also frequently include sample code and supporting technical notes or other supporting documentation to help clarify points made by the primary reference material.</p>","fields":{"slug":"/computer-science/what-is-an-sdk/"},"frontmatter":{"title":"What is an SDK"}}},"pathContext":{"slug":"/computer-science/what-is-an-sdk/"}}

